
// Calificaciones - blog

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;

.contenedorComentarios{
  margin-top: 5rem;
  border: solid 1px darken(c.$gris, 5%);
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: c.$orange-50;

  .linea{
    width: 80%;
    border-color: c.$amarillo;
  }

  .calificaciones{
    display: block;

    @include m.tablet{
      display: grid;
      gap: 1rem;
      grid-template-columns: 74% 25%;
    }

    .tresComentarios{
      margin-bottom: 1rem;

  

      li{
        margin-bottom: 3rem;

    
        @include m.tablet{
          display: flex;
          margin-top: 0;
        }
              
        img{
          max-width: 12rem;

        }

        .izquierda{
          text-align: center;

          @include m.tablet{
            margin-bottom: 0.5rem;
          }

          h5{
            margin: 0.1rem;
            font-size: 1.5rem;
          }

          p{
            margin: 0;
            font-size: 1rem;
          }
    
        }
        
        .centro{
          @include m.tablet{
            margin-left: 2rem;
          }
          p{
            font-size: 1.5rem;
          }
          .checked{
            color: c.$amarillo;
          }

        }

      }

      li:last-child{
        .linea{
          display: none
        };
      }
      li:first-child{
        margin-top: 2rem;
      }

    }

    .unComentario{
      margin-bottom: 3rem;
      .calificacion{
          background-color: c.$verde;
          border-radius: 1rem;
          width: 100%;
          height: 100%;
          position: relative;
          padding: 1rem;
          margin: 0 auto;
    
          &::before{
            content: '';
            background-image: url('../img/comilla.svg');
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: 0.5rem;
          }
  
          .cabecera{
              display: block;
              padding: 0.1rem 2rem 0rem 5rem;

              img{
                margin-left: 2rem;
                width: 12rem;
              }
              
              h4{
                color: c.$blanco;
                font-size: 2rem;
              }
              p{
                font-size: 1.5rem;
              }
              
              .checked{
                color: c.$amarillo;
              }
          }

          .cuerpo{
            padding: 2rem;
              p{
                color: c.$blanco;
                font-size: 2rem;
              }
          }
        }
    }
  }

  .botones{
    a{
      margin: 0.5rem 0 0.5rem 0;
    }

    @include m.tablet{
      a{
        margin: 0;
  
      }
    }

  }
}