@use 'variables' as v;
@use 'colores' as c;

// Fuentes
@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900');
$fuente_principal: 'Poppins', sans-serif;

$delgada: 300;
$regular : 400;
$bold: 700;
$black: 900;


h1, h2, h3 {
    font-family: $fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: c.$amarillo;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
h4{
    font-size: 2.5rem;
}
h5{
    font-size: 2rem;
}
p {
    color: c.$negro;
    font-size: 1.8rem;
}
