

// Usuarios registrados

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;




.usuarios_registrados{

    .barraBotones{
        display: block;

        @include m.tablet{
            display: flex;
        }

        .boton_verde{
            margin: 0.5rem;

            @include m.tablet{
                margin: 0 0.5rem 0.5rem 0;
            }
        }
    }

    h3{
        margin: 2rem 0 2rem 0;
        padding: 0;
    }

    .cambio_password{
        margin-top: 3rem;
        visibility: hidden;
        display: none;
    }
    .mostrar_form{
        visibility: visible;
        display: block;
    }

}

.contenedor_ofertas_usuarios{
    margin-top: v.$separacion;

    .tabla{
        
        @include m.tablet{
            @include m.grid(3, 2);
            
        }

        ul{
            border: 1px solid darken(c.$amarillo, 10%);
            margin: 0.5rem;
            padding: 0;
            -webkit-box-shadow: 4px 5px 5px 0px rgba(184,180,184,1);
            -moz-box-shadow: 4px 5px 5px 0px rgba(184,180,184,1);
            box-shadow: 4px 5px 5px 0px rgba(184,180,184,1);

            li{
                text-align: center;
                h4{
                    font-size: 1.8rem;
                    margin: 1rem 0 1rem 0;
                    padding: 1rem;
                }
                p{
                    font-size: 1.2rem;
                    text-align: justify;
                    margin: 1rem 0 1rem 0;
                    padding: 1rem;
                }
                h5{
                    font-size: 1.5rem;
                    margin: 1rem 0 1rem 0;
                    padding: 1rem;
                }
                p:last-child{
                    text-align: center;
                    padding: 1rem;
                }
            }
        }

    }

}

.user_comentarios{

    form{
        border: none;
        margin: 0;
        padding: 0;
    }

    .campoUserComen{
        label:first-child{
            font-size: 1.5rem;
        }
        label{
            font-size: 3rem;
        }
    }
    textarea{
        border: none;
    }
}
