

// botones

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;

.boton_amarillo{
    @include m.boton(c.$amarillo, c.$blanco, inline-block); // Ocupa solo el espacio que necesita
}
.boton_amarillo-block{
    @include m.boton(c.$amarillo, c.$blanco, block);        // Ocupa todo el espacio disponible
}
.boton_verde{
    @include m.boton(c.$verde, c.$blanco, inline-block);
}
.boton_verde-block{
    @include m.boton(c.$verde, c.$blanco, block);
}
.boton_rojo{
    @include m.boton(c.$rojo, c.$blanco, block);
}
.boton_rojo-block {
    @include m.boton(c.$rojo, c.$amarillo, inline-block);
}

.boton_verde-igual{
    @include m.botones_iguales(c.$verde, c.$blanco);
}