
// Acciones SCSS

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;


.acciones{
    margin-top: v.$separacion;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    a{
        color: c.$amarillo;
        display: block;
        margin-bottom: calc(v.$separacion / 2);
        text-align: center;
    }
}


