
// Footer

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


footer{
    background-color: c.$orange-300;
    height: 15rem;
    padding: 2rem;
    margin: 5rem 0 0 0;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    
   
    .contenedor_footer{
        padding-top: 1rem;
        nav{
            color: c.$blanco;
        }
    }
  

    .derechos{
       display: grid;
       align-items: end;
        p{
            text-align: end;
            font-size: 1rem;
            color: c.$blanco;
        }
    }

    .navegacion_footer{
        display: inline-block;
        flex-direction: column;
        align-items: flex-start;
        a{
            padding-left: 1.5rem;
            color: c.$blanco;
            text-decoration: none;
            font-size: 1.7rem;
            font-weight: t.$bold;
        }
    }
}