 
 // Contenedor cartas

 @use 'base/variables' as v;
 @use 'base/mixins' as m;
 @use 'base/colores' as c;
 @use 'base/tipografia' as t;
 

.contenedorCartas{ 
    margin-top: v.$separacion;
    
    @include m.tablet {
       display: grid;
       grid-template-columns: repeat(3, 1fr);
    }
    .cartas{
        margin: 1rem;
        border: 1px solid darken(c.$gris, 10%);

        -webkit-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
        -moz-box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);
        box-shadow: 4px 4px 5px 0px rgba(143,139,143,1);

        .conteTitulo{
            padding: 2rem;
            h3{
                margin: 0;
                @include m.tamañofuente(0.8em, 0.8vw)
            }
        }
        .conteImg{
            img{
                width: 100%;
                height: 20rem;
            }
        }
        .conteBoton{
            padding: 1.5rem;
            a{
                margin: 0;
            }
        }
    }
}

