
// darkMode

@use 'variables' as v;
@use 'colores' as c;

.dark_mode{
    background-color: c.$dark;

    p{
        color: c.$blanco;
    }
    a, h1, h2, h3, h4{
        color: c.$gris;
    }

    .formulario{

        label,
        p,
        legend{
            color: c.$gris;
        }

        input:not([type="submit"]),
        textarea,
        select{
            background-color: c.$dark;
            border: solid 1px c.$dark;
            color: c.$blanco;
        }
    }

}