
// Redes sociales

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.social{
    margin-top: 10rem;
    margin-bottom: 10rem;
    text-align: center;
    border: solid 1px darken(c.$gris, 10);
    padding: 2rem;
    
    .contenido_social{
        @include m.tablet{
            @include m.grid(4, 2rem);
        }

        @include m.grid(2, 2rem);
        

        img{
            width: 10rem;
            height: 10rem;
        }
    }
}