
// Alertas SCSS

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.alerta{
    padding: calc(v.$separacion / 4);
    color: c.$blanco;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: t.$bold;
    text-transform: uppercase;

    &.error{
        background-color: c.$rojo;
    }
    &.exito{
        background-color: c.$verde;
    }
}