
// estrellas


@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;


.comentarios{
  background-color: c.$blanco;
    
  .clasificacion{
        display: flex;
        direction: rtl;
        unicode-bidi: bidi-override;
        width: 25rem;

        input[type="radio"] {
            display: none;
        }

        label{
            color: c.$gris;
            text-shadow: 1px 1px rgba(0,0, 0, 3);
            padding: 1rem;
            margin: 0;
            font-size: 3rem;
        }
        label:hover,
        label:hover ~ label {
          color: orange;
        }
        
        input[type="radio"]:checked ~ label {
          color: orange;
        }
        
    }
    .checked{
      color: c.$amarillo;
    } 
}