
// Para aplicar a todos los formularios por igual

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.contenedor_formulario{
    border: solid 1px darken(c.$amarillo, 10);
    border-radius: 1rem;
    padding: 1rem;
    width: 100%;
    margin-top: 3rem;

    fieldset{
        text-align: left;
        padding: 0 2rem 0 2rem;
        border: none;

    }

    legend{
        font-size: 2rem;
        color: c.$grisOscuro;
    }

    .campo {
        display: flex;
        width: 100%;
        margin: 1rem 0 0 0;
        border-radius: 1rem;
        border: solid 1px darken(c.$gris, 10);
        align-items: center;
        text-align: start;


        label{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 15rem;
            font-weight: t.$bold;
            text-transform: uppercase;
            padding: 1rem;
        }
    
        input{
            width: 100%;
            border: none;
            margin: 0;
            padding: 0;
            padding: calc(v.$separacion / 4);
            border-radius: 10px;
            text-align: left;
        }

        textarea{
            border: none;
            padding: 1rem;
            width: 100%;
            height: 100%;
            background-color:  c.$blanco;
            border-radius: 1rem;
        }
    
    }

    .contenedor_imagen{
        text-align: center;
        margin-top: 3rem;
        margin-bottom: 3rem;
        img{
            width: 20rem;
        }
    }

    .campo_label{
        display: flex;
        padding: 1rem;
        border-radius: 1rem;
        border: solid 1px darken(c.$gris, 10);
        label{
            width: auto;
        }
        input{
            width: auto;
            margin-left: 5rem;
        }
    }
    
   .botones_usuarios{
    display: flex;
    justify-content: space-between;
    margin-right: 1rem;
   }
}