

// Panel control

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.panel_control{
    margin-top: v.$separacion;

    .contenido{
        display: block;
        margin: 0;
        padding: 0;

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
        }

        h4{
            font-size: 1.5rem;
        }
        p{
            font-size: 1.2rem;
        }

        .informe{
            border: 1px solid darken(c.$gris, 10);
            background-color: c.$amber-200;
            border-radius: 1rem;
            box-shadow: 1px 12px 9px -13px rgba(0, 0, 0,.4);
            margin: 0 0 1rem 0;
            
            @include m.tablet{
                margin: 0;
            }
            
        }
    }




}