
// Contacto

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contacto{
    margin-top: v.$separacion;
    height: auto;
        .pedidos{
        border: 1px solid darken(c.$gris, 10);
        border-radius: 1rem;
        padding: 2rem;

        .texto_pedidos{
            img{
                max-width: 6rem;
            }
        }
    }

    .local{
        margin: 5rem 0 5rem 0;
        .imagenes_local{
            display: flex;
        
            img{
                height: 100%;
            }
        }
        line-height: 1.4;
        &::after{
        content: '';
        display: block;
        width: 100%;
        height: 0.2rem;
        background-color: c.$amarillo;
        margin-top: 4rem;
    }
    }

    .mapa{

        .map_google{
            width: 60%;
            height: 30rem;
        }
    }

}