
// Mixins

@use 'variables' as v;
@use 'colores' as c;
@use 'tipografia' as t;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

// Botones
@mixin boton($bgColor, $colorTexto, $display) {
    background-color: $bgColor;
    display: $display;

    color: $colorTexto;
    font-weight: t.$bold;
    font-size: 2rem;
    text-align: center;
    
    margin: 0.2rem 0;
    padding: 1.5rem 3rem;


    border: none;

    transition-property: background-color;
    transition-duration: .3s;
    
    width: 100%;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin botones_iguales($bgColor, $colorTexto) {
    background-color: $bgColor;
    color: $colorTexto;
    display: block;
    
    font-weight: t.$bold;
    font-size: 2rem;
    text-align: center;
    
    margin: 0.5rem 0.5rem 0 0.5rem;
    padding: 1.5rem 3rem;

    border: none;

    transition-property: background-color;
    transition-duration: .3s;
    
    width: 100%;

    @include tablet {
        width: 33%;
    }

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin accion_boton(){
    transition-property: background-color;
    transition-duration: .3s;
    &:hover {
        background-color: darken( c.$amarillo, 10% );
        cursor: pointer;
    }
}

// Grid
@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin tamañofuente($em, $vw){
    font-size: calc($em + $vw);
}
