
// Nosotros

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.nosotros{
    margin-top: v.$separacion;
    

    .contenidoHistoria, .contenidoEquipo{
        margin-bottom: 3rem;

        h4{
            margin: 0;
        }
        p{
            margin: 0;
            padding: 0;
        }

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);

            .texto{
                padding: 0 1.5rem 0 1.5rem;
                grid-column: 2 / 3;
                h4{
                    margin: 0 0 1rem 0;
                }
                p{
                    margin: 0;
                    padding: 0;
                }
      
            }
            .imagen_uno{
                grid-row: 1 / 2;
                grid-column: 1 / 2;
            }
            .imagen_dos{
                grid-row: 1 / 2;
                grid-column: 3 / 4;
            }
        
        }
    
        p{
            text-align: justify;
            padding: 2rem;
        }

        img{
            width: 35rem;
            height: 30rem;
        }

    }

    .contenidoEquipos{
        display: block;

        .contenidoMaquinas{
            img{
                width: 80%;
                height: 25rem
            }

            @include m.tablet{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                gap: 1rem;    
                img{
                    width: 100%;
                }
            }


        }

    }
}