// Aside

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;



.dashboard{
    display: flex;
    height: 100%; 
}


.mobile_dash{
    display: inline;

    .nav_dash{
        background-color: c.$brown-800;
        width: 5rem;
        height: 4rem;
        padding: 0;
        margin: 0;
    }
    @include m.tablet{
        display: none;
    }
}

.contenedor_aside{
    background-color: c.$brown-800;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    height: 150vh; 

    width: 0rem;
    opacity: 0;
    transform: translateX(0rem);
    transition: all .75s ease;
    visibility: hidden;

    @include m.tablet{
        visibility: visible;
        opacity: 1;
        display: flex;
        width: 20rem;
    }
    
        .conte_cabecera{
            text-align: left;

            .cabecera{
                .conte_logo{
                    text-align: center;
                    .logo{
                        width: 10rem;
                    }
                }

                .conte_titulo{
                    text-align: center;
                    margin-top: 2rem;
                    a{
                        color: c.$blanco;
                        font-weight: t.$bold;
                    }
                }
            }


            .sidebar{
                display: block;
                width: auto;
                padding: 0;
                margin: 1rem;

                .navegaciones{

                    .menu_dash{
                        color: c.$blanco;
                        display: flex;
                        align-items: center;
                        text-align: left;
                        margin-bottom: 0.5rem;
                        padding: 0.5rem;
                        transition-property: backgroud-color;
                        transition-duration:  300ms;
            
                        &:hover{
                            background-color: darken( rgb(202, 148, 241), 20%);
                            border-radius: 0.5rem;
                        }
            
                        span{
                            padding-left: 1rem;
                            font-size: 1.5rem;
                        }

                        .img_navega{
                            width: 1.8rem;
                        }
                    }
            
                    nav{
                
                        a{
                            color: c.$blanco;
                            display: block;
                            margin-bottom: 0.5rem;
                            padding: 0.5rem;
                            transition-property: backgroud-color;
                            transition-duration:  300ms;
                            margin-left: 1.8rem;
                            text-align: left;
                            font-size: 1.2rem;

                            &:hover{
                                background-color: darken( rgb(202, 148, 241), 20%);
                                border-radius: 0.5rem;
                            }
                        }

                        .dark_mode_boton{
                            width: 2.5rem;
                        }
                
                    }
                    
                    .navega-1,
                    .navega-2,
                    .navega-3,
                    .navega-4,   
                    .navega-5,
                    .navega-6   {
                        visibility: hidden;
                        opacity: 0;
                        height: 0;
                        transition: visibility .2s ease-in-out, opacity 0.5s linear, height 0.5s linear;
                        text-align: center;
                    }

                    .mostrar_navega{
                        visibility: visible;
                        opacity: 1;
                        height: auto;
                    }
                }
            }
        }


        .cerrar_sesion{

            a{
                text-align: center;
                color: c.$blanco;
                display: block;
                margin-bottom: 0.5rem;
                padding: 0.7rem;
                transition-property: backgroud-color;
                transition-duration:  300ms;


                &:hover{
                    background-color: darken( rgb(202, 148, 241), 20%);
                    border-radius: 0.5rem;
                }
            }
        }
    }

    .mostrar_contenedor_aside{
        visibility: visible;
        opacity: 1;
        display: flex;
        width: 20rem;
    }

    .contenido_dash{
        display: block;
        width: 100%;

        .barra_dash{
            background-color: c.$amarillo;
            box-shadow: 1px 12px 9px -13px rgba(0, 0, 0,.4);
            padding: 2rem;
            display: flex;
            justify-content: space-between;
        }
    }

