
 // Tablas 

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;




.contenedor_tablas{
    margin-top: v.$separacion;

    .contenedor_botones{
        display: flex;
        justify-content: space-between;

        a{
            margin: 0 0 1rem 0;
            padding: 1rem;
            border-radius: 1rem;
        }
    }

    .contenedor_tabla{
        padding: 1rem;
        border-radius: 1rem;
        box-shadow: 1px -10px 20px -5px rgba(0, 0, 0,.4);
        margin-bottom: 5rem;
    }

    .head_tabla{
        
        th{
            text-align: center;
            font-size: 1rem;
        }
    }

    // Body
    tbody{
        td{
            text-align: center;
        }
        .imagen{
            width: 10rem;
        }
    
        .imagenCate{
            width: 20rem;
        }
        .categoria,
        .oferta,
        .media{
            font-size: 1.2rem;
        }
        .precio,
        .precio_ofer,
        .precio_med{
            font-size: 1.4rem;
        }

        .titulo{
            font-size: 1.5rem;
            font-weight: t.$bold;
            text-align: left;
        }
        .descripcion{
            font-size: 1rem;
            text-align: left;
        }

        .creada{
            font-size: 0.9rem;
        }
        .actualiza,
        .elimina{
            width: 4rem;
        }
    }
}