
// Seccion encargo

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.no-webp .encargo{
    background-image: url('/build/img/pollo_asado_barra.jpg');
}
.webp .encargo{
    background-image: url('/build/img/pollo_asado_barra.webp');
}
.encargo{
    background-position: center center;
    background-size: cover;
    margin-top: v.$separacion;
    position: relative;
    height: 40rem;


    .fondo_encargo{
        padding: calc(v.$separacion * 2);
        display: flex;
        flex-direction: column;
        align-items: center;
       
        position: absolute;
        background-color: rgb(0 0 0 / 45%);
        width: 100%;
        height: 40rem;
        
        
        h2{
            font-size: 4rem;
            font-weight: t.$bold;
            color: c.$blanco;
        }
        P{
            font-size: 1.8rem;
            color: c.$blanco;
        }
    }
  
}
