// Ofertas

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.contenedor_ofertas{
    margin-top: v.$separacion;

    .botones{
        text-align: left;

        a{
            padding: 1rem ;
            margin-bottom: 1rem;
        }
    }

    .tabla_a{
        display: block;
        padding: 0;

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 1rem;
        }

        .seleccion{
            text-align: center;
            padding: 1rem;
            margin-bottom: 1rem;
            height: 100%;
            color: c.$blanco;
            background-color: c.$orange-200;

            -webkit-box-shadow: 10px 10px 5px -6px rgba(179,177,179,1);
            -moz-box-shadow: 10px 10px 5px -6px rgba(179,177,179,1);
            box-shadow: 10px 10px 5px -6px rgba(179,177,179,1);

            h4{
                font-size: 2rem;
                margin: 0.5rem 0 0.5rem 0;
            }
            p{
                text-align: left;
                font-size: 1.2rem;
                margin: 0.5rem 0 0.5rem 0;
            }
            h5{
                font-size: 1.3rem;
                margin: 0.5rem 0 0.5rem 0;
                color: c.$amarillo;
            }
            p:last-child{
                color: c.$blanco;
                font-size: 1.3rem;
                text-align: center;
                text-transform: uppercase;
                font-weight: t.$bold;
            }
        }
        .seleccion:nth-child(2n + 1){
            background-color: c.$orange-100;
        }
    }
}