
// Todas las calificaciones

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.contenedor_blog{

    // Articulos
    .articulos{
        
        li{
            .articulo{
                width: 100%;
                min-height: 35rem;
                background-color: antiquewhite;
                border: 1px solid c.$amarillo;
                border-radius: 1rem;
                padding: 1rem;
                margin: 1rem;
    
            }
            .articulo img{
                width: 20rem;

                @include m.tablet{
                    width: 30rem;
                    float: left;
                    margin-top: -2rem;
                }
            }
        }
        li:nth-child(2n+1){
            .articulo{
                background-color: c.$orange-50;
            }
            .articulo img{
                @include m.tablet{
                    float: right;
                }
            }
        }


       
        .lineaAbajo{
            background-color: c.$verde;
            width: 90%;
            height: 0.2rem;
        }
    }
    // Fin seccion articulos

    // Seccion listado comentarios
    .comentarios{
        margin-top: 3rem;

        .contenido_blog{
            border: 1px solid c.$amarillo;
            border-radius: 1rem;
            padding: 1rem;

            .tabla_blog{
                display: grid;
                grid-template-columns: 1fr 4fr 1fr;
                margin: 0;
                padding: 0.5rem;
                border-bottom: 1px solid darken(c.$amarillo, 10%);
                
                img{
                    max-width: 12rem;

                }
                h5{
                    margin: 0;
                    padding: 0.5rem;
                    font-size: 1.5rem;
                }
                p{
                    margin: 0;
                    padding: 0.5rem;
                    font-size: 1.2rem;
                }
                

                .columna_I{
                    display: block;
                    text-align: center;
                    p{
                        font-size: 1rem;
                    }
                }

                .columna_C{
                    display: block;
                    border-left: 1px solid darken(c.$orange-100, 10%);
                    //border-right: 1px solid darken(c.$orange-100, 10%);
                    padding: 0 0 0.5rem 2rem;

                    .comentarios{

                        p{
                            width: 9rem;
                            font-size: 1.5rem;
                        }
                    }

                }

                .columna_D{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 1rem;
                }
            }
            ul:last-child{
                border-bottom: none;
            }
        }
    }
    // Fin de listado

}