// Botones barra superior

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;


.barraBotones{        // Cambio secciones

    @include m.tablet{
        display: flex;
    }

    button{
        display: block;
        height: 6rem;
        padding: 2.5rem;
        background-color: antiquewhite;
        border: none;
        color: c.$amarillo;
        width: 100%;
        margin: 0 0 0.5rem 0;
        transition: background-color 300ms ease;

        &:hover{
            cursor: pointer;
            background-color: darken(antiquewhite, 10%);
            color: c.$blanco;
            font-weight: t.$bold;
        }
       
        &.actual{
            background-color: c.$amarillo;
            color: c.$blanco;
            font-weight: t.$bold;
        }

    }
}
