// Utilidades

@use 'variables' as v;


.seccion {
    margin-top: calc(v.$separacion / 2);
    margin-bottom: calc(v.$separacion / 2);
}
.contenido_centrado {
    max-width: 100rem;
    text-align: center;
    margin-top: v.$separacion;
}

.alinear_derecha{
    display: flex;
    justify-content: flex-end;
}
.alinear_izquierda{
    display: flex;
    justify-content: flex-start;
}
.w-100{
    width: 100%;
}

.mostrar_password{      // Muestra el contenido del campo password 
    position: relative;
    width: 100%;
    img{
        position: absolute;
        width: 4rem;
        top: -0.5rem;
        right: 1rem;
        filter: opacity(20%);
    }

    .mostrarPass{
        visibility: visible;
    }
    
    .ocultarPass{
        visibility: hidden;
    }

}
 

.secciones{
    display: none;
}

.mostrar{
    display: block;
    visibility: visible;
}

.ocultar{
    display: none;
    visibility: hidden;
}

.mostrar_boton{
    visibility: visible;
}
.oculta_boton{
    visibility: hidden;
    display: none;
}