
// Carta general

@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'base/colores' as c;
@use 'base/tipografia' as t;

.cartas{
    margin-top: v.$separacion;
    
    .tabla{
        margin: 0;
        padding: 0;

        @include m.tablet{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }

        .seleccion{


            img{
                width: 100%;
                height: 22rem;
                margin: 0;
            }
            h4{
                color: c.$blanco;
                background-color: c.$amarillo;
                padding: 1rem 0 1rem 0;
                margin: 2rem 0 0 0;
            }
        }

    }
}

